/* VARIABLES */
:root {
  --accent-color: #64ffda;
  --background-color: #0a192f;
  --highlight-color: #e6f1ff;
  --text-color: #64ffda;
  --button-font-size: 2rem;
}

/* MEDIA QUERIES */
@media screen and (max-width: 1400px) {
  .abt-title,
  .cnt-title,
  .exp-title,
  .prj-title {
    font-size: var(--button-font-size);
  }

  #profile {
    height: 83vh;
    margin-bottom: 0;
  }

  .about-containers {
    flex-wrap: wrap;
  }

  #contact,
  #projects {
    height: fit-content;
  }

  .p-detail {
    text-align: center;
  }
  .btn-color-1,
  .btn-color-2 {
    border: var(--accent-color) 0.1rem solid;
    color: var(--accent-color);
  }

  .btn-color-1:hover,
  .btn-color-2:hover {
    cursor: pointer;
  }

  .btn-color-2:hover {
    background: var(--background-color);
    color: var(--accent-color);
  }

  .btn-color-1 {
    background-color: var(--highlight-color);
    color: var(--background-color);
    border: 1px solid var(--accent-color);
  }

  .btn-color-1:hover {
    background: var(--background-color);
    color: var(--accent-color);
    border: 2px solid var(--accent-color);
  }

  .btn-color-2 {
    background: none;
  }

  .btn-color-2:hover {
    border: var(--highlight-color) 0.1rem solid;
  }
  .btn:active {
    transform: scale(0.9);
    box-shadow: var(--highlight-color) 0px 7px 1px 0px;
    letter-spacing: 0.6px;
  }
}

@media screen and (max-width: 1200px) {
  #desktop-nav {
